"use client";

import { useState } from "react";
import { motion } from "framer-motion";

export default function RegisterPage() {
	const [step, setStep] = useState(1);
	const [age, setAge] = useState("");
	const [gender, setGender] = useState("");
	const [password, setPassword] = useState("");
	const [userId, setUserId] = useState(null);

	const generateUserId = () => {
		return "XC" + Math.floor(100000 + Math.random() * 900000);
	};

	const handleRegister = () => {
		if (!gender || !password) {
			alert("Gender and password are required!");
			return;
		}

		const newUserId = generateUserId();
		setUserId(newUserId);

		localStorage.setItem(
			"user",
			JSON.stringify({ age, gender, password, userId: newUserId })
		);
	};

	return (
		<div className="flex flex-col items-center justify-center h-screen bg-black text-white">
			<h2 className="text-3xl font-bold mb-6 text-pink-500">
				Register on xConfess
			</h2>

			{userId ? (
				<motion.div
					initial={{ opacity: 0, y: 50 }}
					animate={{ opacity: 1, y: 0 }}
					className="bg-gray-900 p-6 rounded-lg text-center"
				>
					<p className="text-green-400 text-lg font-semibold">
						Registration Successful! ðŸŽ‰
					</p>
					<p className="text-white mt-2">Your User ID:</p>
					<p className="text-pink-500 text-2xl font-bold">{userId}</p>
					<p className="text-sm mt-2">Copy and save it for login.</p>
				</motion.div>
			) : (
				<div className="w-80 bg-gray-900 p-6 rounded-lg shadow-lg">
					{step === 1 && (
						<motion.div
							initial={{ opacity: 0, x: -50 }}
							animate={{ opacity: 1, x: 0 }}
							transition={{ duration: 0.5 }}
						>
							<label className="block text-white mb-2">
								Age (Optional)
							</label>
							<input
								type="number"
								placeholder="Enter Age"
								value={age}
								onChange={(e) => setAge(e.target.value)}
								className="w-full p-2 border border-gray-700 bg-gray-800 text-white rounded"
							/>
							<button
								onClick={() => setStep(2)}
								className="mt-3 w-full bg-pink-500 text-white p-2 rounded hover:bg-pink-600 transition"
							>
								Next
							</button>
						</motion.div>
					)}

					{step === 2 && (
						<motion.div
							initial={{ opacity: 0, x: -50 }}
							animate={{ opacity: 1, x: 0 }}
							transition={{ duration: 0.5 }}
						>
							<label className="block text-white mb-2">
								Gender (Required)
							</label>
							<select
								value={gender}
								onChange={(e) => setGender(e.target.value)}
								className="w-full p-2 border border-gray-700 bg-gray-800 text-white rounded"
							>
								<option value="">Select Gender</option>
								<option value="Male">Male</option>
								<option value="Female">Female</option>
								<option value="Other">Other</option>
							</select>
							<button
								onClick={() => setStep(3)}
								className="mt-3 w-full bg-pink-500 text-white p-2 rounded hover:bg-pink-600 transition"
							>
								Next
							</button>
						</motion.div>
					)}

					{step === 3 && (
						<motion.div
							initial={{ opacity: 0, x: -50 }}
							animate={{ opacity: 1, x: 0 }}
							transition={{ duration: 0.5 }}
						>
							<label className="block text-white mb-2">
								Password (Required)
							</label>
							<input
								type="password"
								placeholder="Enter Password"
								value={password}
								onChange={(e) => setPassword(e.target.value)}
								className="w-full p-2 border border-gray-700 bg-gray-800 text-white rounded"
							/>
							<button
								onClick={handleRegister}
								className="mt-3 w-full bg-green-500 text-white p-2 rounded hover:bg-green-600 transition"
							>
								Register
							</button>
						</motion.div>
					)}
				</div>
			)}
		</div>
	);
}
